import react, { useState } from "react";
import { Card, CardContent, TextField, Button, CircularProgress, Grid } from "@material-ui/core";
import socket from "../socket";
import "./GamePage.style.css";

const GamePage = () => {
    const [timer, setTimer] = useState(null);
    const [data, setData] = useState(null);
    const [waiting, setWaiting] = useState(true);
    const [seconds, setSeconds] = useState(0);
    const time = 20;
    socket.on("question", (req) => {
        setData(req);
        setWaiting(false);
        setTimer(setInterval(() => {
            setSeconds(seconds + 1);
            if (seconds == time) {
                socket.emit("timeup");
            }
        }, 1000));
    });

    function fixUmlauts(value) {
        return decodeURI(value);
    }
    const answer = (index) => {
        socket.emit("answer", index);
        clearInterval(timer);
        setWaiting(true);
    }

    return (
        <>
            <Card>
                <CardContent>
                    {
                        waiting ?
                            <>
                                <CircularProgress />
                                <p>Waiting for question...</p>
                            </>
                            :
                            <>
                                <h1>{fixUmlauts(data.question)}</h1>
                                <Button size="large" variant="outlined" onClick={() => { answer(0) }}>{fixUmlauts(data.answers[0])}</Button>
                                <Button size="large" variant="outlined" onClick={() => { answer(1) }}>{fixUmlauts(data.answers[1])}</Button>
                                <Button size="large" variant="outlined" onClick={() => { answer(2) }}>{fixUmlauts(data.answers[2])}</Button>
                                <Button size="large" variant="outlined" onClick={() => { answer(3) }}>{fixUmlauts(data.answers[3])}</Button>
                                <p>Remaining time: {time - seconds} seconds</p>
                            </>

                    }
                </CardContent>
            </Card>
        </>);
}

export default GamePage;
